
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import { validateNotNull, validateUpload } from 'util/rules'
import Uploader from 'assembly/uploader/uploader'
export default {
data () {
return {
switchIndex: 0,
photoTree: [
{label: '专卖店正面照片', value: 'photo'},
{label: '专卖店内部照片', value: 'photo2'},
{label: '专卖店左侧照片', value: 'photo3'},
{label: '专卖店右侧照片', value: 'photo4'}
],
photoTree2: [
{label: '上传合同封面照片', value: 'pFrontPhoto'},
{label: '上传合同首页照片', value: 'pCoverPhoto', img: 'pCoverPhoto'},
{label: '上传合同签名页(含甲乙双方)', value: 'pSignPhoto', img: 'pSignPhoto'}
],
pCoverPhoto: require('../../assets/images/pCoverPhoto.jpeg'),
pSignPhoto: require('../../assets/images/pSignPhoto.jpeg'),
userInfo: {},
dicts: [], // 字典值
fwi: {
ownerSex: '', // 店主性别
shopkeeperage: '', // 店主年龄
saleMan: '', // 业务员
hasConsultant: '',
homeConsultantNum: '',
homeConsultants: '',
salesCount: '', // 人员配置
setupEmp: '', // 开店员工
empDegree: '', // 员工身份
hasStationEmp: '', // 是否有驻店员工
bopExplain: '', // BOP讲解
outletArea: '', // 面积
rentCost: '', // 租金
clerkmonthlysalary: '', // 店员月工资
monthSalesAvg: '', // 平均月销售
profitAndLoss: '', // 月盈亏平衡
addressSource: '', // 地址来源
msgSource: '', // 信息来源


visualizeGrade: '', // 店面形象
photo: '', // 专卖店图片(正)
photo2: '', // 专卖店图片2(内)
photo3: '', // 专卖店图片3(左)
photo4: '' 
},
contract: {
pDbProtocolNo: '', // 合同编号
pOrganName: '', // 合同客户名称
pCardNo1: '', // 身份证号码
pDbProtocolAddr: '', // 合同联系地址
pDbProtocolDateE: '', // 合同有效期结束日期
pDbProtocolDateS: '', // 合同有效期开始日期
pFrontPhoto: '',
pCoverPhoto: '', // 合同首页照片
pSignPhoto: '' 
},
gender: false,
home_consultant: false,
emp_degree: false,
has_station_emp: false,
bop_explain: false,
address_source: false,
msg_source: false,


visualize_grade: false,
hasConsultant: '',
homeConsultants: [],
ownerSex: '',
empDegree: '',
hasStationEmp: '',
bopExplain: '',
addressSource: '',
msgSource: '',


visualizeGrade: '',
photo: '',
photo2: '',
image: '',
dialogVisible: false,
inited: false 
}
},
methods: {
goBack () {
this.$util.getMachine(4)
},
switchTabItem (index) {
this.switchIndex = index
},
beforeSend (file) {
if (validateUpload(this, file)) {
this.$vux.loading.show({
text: '图片上传中，请稍候'
})
return true
}
},
showImg (img) {
this.image = this[img]
this.dialogVisible = true
},
imgClick (value) {
this.photo = value
},
imgClick2 (value) {
this.photo2 = value
},
onFinish (res) {
this.$vux.loading.hide()
this.fwi[this.photo] = res.model
},
onFinish2 (res) {
this.$vux.loading.hide()
this.contract[this.photo2] = res.model
},
onError () {
this.$vux.loading.hide()
this.$vux.toast.text('上传失败，请重试~')
},
showSelector (item) {
this[item] = true
},
addHomeConsultant () {
if (this.homeConsultants.length < 4) {
this.homeConsultants.push({
name: '',
phone: ''
})
} else {
this.$vux.toast.text('最多可以填写4个HCC信息')
}
},
delHomeConsultant (index) {
if (this.homeConsultants.length === 1) {
this.$vux.toast.text('至少填写1个HCC信息')
return
}
this.homeConsultants.splice(index, 1)
},
setHomeConsultant (key, item) {
this.hasConsultant = item.label
if (key === 'false') {
this.homeConsultants = []
} else {
this.homeConsultants = [{
name: '',
phone: ''
}]
}
},
setGender (key, item) {
this.ownerSex = item.label
this.fwi.ownerSex = key
},
setEmpDegree (key, item) {
this.empDegree = item.label
this.fwi.empDegree = key
},
setStationEmp (key, item) {
this.hasStationEmp = item.label
this.fwi.hasStationEmp = key
},
setBopExplain (key, item) {
this.bopExplain = item.label
this.fwi.bopExplain = key
},
setAddressSource (key, item) {
this.addressSource = item.label
this.fwi.addressSource = key
},
setMsgSource (key, item) {
this.msgSource = item.label
this.fwi.msgSource = key
},








setVisualizeGrade (key, item) {
this.visualizeGrade = item.label
this.fwi.visualizeGrade = key
},
validateInt (value, field) {
if (!validateNotNull(this, value, field)) {
return false
} else if (!/^[+]?\d+(.\d+)?$/.test(value)) {
this.$vux.toast.text(field + '必须>=0')
return false
}
return true
},
submit () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
console.log(this.fwi, this.contract)
let flag = validateNotNull(this, this.fwi.ownerSex, '店主性别') && validateNotNull(this, this.fwi.shopkeeperage, '店主年龄') && validateNotNull(this, this.fwi.saleMan, '业务员') && validateNotNull(this, this.hasConsultant, '是否有理家顾问')
if (!flag) {
return
}
if (this.hasConsultant === '是') {
let ifFill = true
this.homeConsultants.forEach(item => {
if (!(item.name && item.phone)) {
ifFill = false
}
})
if (!ifFill) {
this.$vux.toast.text('请完善理家顾问信息')
return
}
}
if (validateNotNull(this, this.fwi.salesCount, '人员配置') && validateNotNull(this, this.fwi.setupEmp, '开店员工') && validateNotNull(this, this.fwi.empDegree, '员工身份') &&
validateNotNull(this, this.fwi.hasStationEmp, '是否有驻店员工') && validateNotNull(this, this.fwi.bopExplain, 'BOP讲解') && validateNotNull(this, this.fwi.outletArea, '专卖店面积') &&
validateNotNull(this, this.fwi.rentCost, '月租金') && validateNotNull(this, this.fwi.clerkmonthlysalary, '店员月工资') && validateNotNull(this, this.fwi.monthSalesAvg, '平均月销售') && this.validateInt(this.fwi.profitAndLoss, '月盈亏平衡') &&
validateNotNull(this, this.fwi.addressSource, '地址来源') && validateNotNull(this, this.fwi.msgSource, '信息来源') &&
validateNotNull(this, this.fwi.visualizeGrade, '店面形象') && validateNotNull(this, this.fwi.photo, '专卖店正面图片') &&
validateNotNull(this, this.fwi.photo2, '专卖店内部图片') && validateNotNull(this, this.fwi.photo3, '专卖店左侧图片') && validateNotNull(this, this.fwi.photo4, '专卖店右侧图片') &&
validateNotNull(this, this.contract.pDbProtocolNo, '专卖店合同编号') && validateNotNull(this, this.contract.pOrganName, '合同乙方客户名称') && validateNotNull(this, this.contract.pCardNo1, '法人身份证号') &&
validateNotNull(this, this.contract.pDbProtocolAddr, '合同专卖店地址') && validateNotNull(this, this.contract.pDbProtocolDateS, '合同有效期开始日期') && validateNotNull(this, this.contract.pDbProtocolDateE, '合同有效期结束日期') &&
validateNotNull(this, this.contract.pFrontPhoto, '合同封面照片') && validateNotNull(this, this.contract.pCoverPhoto, '合同首页照片') && validateNotNull(this, this.contract.pSignPhoto, '合同签名页')) {
this.fwi.homeConsultantNum = this.hasConsultant === '是' ? this.homeConsultants.length : 0
this.fwi.hasConsultant = this.hasConsultant === '是'
this.fwi.homeConsultants = JSON.stringify(this.homeConsultants)
this.fwi.hasStationEmp = this.fwi.hasStationEmp === 'true' ? true : this.fwi.hasStationEmp === 'false' ? false : this.fwi.hasStationEmp
this.inited = true
this.$tupHttp(this, 'front/user/openning/submit', 'POST', {
contract: this.contract,
fwi: this.fwi
}).then(res => {
this.$vux.toast.text('提交成功！')
setTimeout(() => {
this.$router.go(-1)
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
},
_getInfo () {
this.$tupHttp(this, 'front/user/info/v1').then(res => {
this.userInfo = res.data.model
if (this.userInfo.fwi) {
this.fwi = this.userInfo.fwi
this.ownerSex = this.fwi.ownerSex
this.homeConsultants = this.fwi.hasConsultant ? JSON.parse(this.fwi.homeConsultants) : []
if (this.fwi.hasConsultant) {
this.hasConsultant = '是'
this.homeConsultants = [{
name: '',
phone: ''
}]
} else {
this.hasConsultant = '否'
this.homeConsultants = []
}
this.hasStationEmp = this.fwi.hasStationEmp ? '是' : '否'
this.bopExplain = this.fwi.bopExplain
this.addressSource = this.fwi.addressSource
this.msgSource = this.fwi.msgSource

this.outletShow = this.fwi.outletShow
this.visualizeGrade = this.fwi.visualizeGrade
this.fwi.ownerSex = this.fwi.ownerSex ? this.dicts.gender.filter((item) => {
return item.label === this.fwi.ownerSex
})[0].value : this.fwi.ownerSex
this.fwi.hasStationEmp = this.fwi.hasStationEmp ? 'true' : 'false'
this.fwi.bopExplain = this.fwi.bopExplain ? this.dicts.bop_explain.filter((item) => {
return item.label === this.fwi.bopExplain
})[0].value : this.fwi.bopExplain
this.fwi.addressSource = this.fwi.addressSource ? this.dicts.address_source.filter((item) => {
return item.label === this.fwi.addressSource
})[0].value : this.fwi.addressSource
this.fwi.msgSource = this.fwi.msgSource ? this.dicts.msg_source.filter((item) => {
return item.label === this.fwi.msgSource
})[0].value : this.fwi.msgSource






this.fwi.visualizeGrade = this.fwi.visualizeGrade ? this.dicts.visualize_grade.filter((item) => {
return item.label === this.fwi.visualizeGrade
})[0].value : this.fwi.visualizeGrade
}
if (this.userInfo.contract) {
this.contract = this.$util.deepClone(this.userInfo.contract)
this.contract.pDbProtocolDateS = this.contract.pDbProtocolDateS ? new Date(this.contract.pDbProtocolDateS).format('yyyy-MM-dd') : this.contract.pDbProtocolDateS
this.contract.pDbProtocolDateE = this.contract.pDbProtocolDateE ? new Date(this.contract.pDbProtocolDateE).format('yyyy-MM-dd') : this.contract.pDbProtocolDateE
}
})
},
_getDicts () {
let data = ['visualize_grade', 'msg_source', 'address_source', 'gender', 'bop_explain']
this.$tupHttp(this, 'front/dict/getDicts', 'POST', data).then(res => {
this.dicts = res.data.model
this.dicts.gender = this.dicts.gender.map((item, index) => {
let sItem = {}
sItem.label = item.dictName
sItem.value = item.dictValue
return sItem
})
this.dicts.has_station_emp = [{label: '是', value: 'true'}, {label: '否', value: 'false'}]
this.dicts.emp_degree = [{label: 'DFS', value: 'DFS'}, {label: 'DM', value: 'DM'}, {label: 'PCD', value: 'PCD'}]
this.dicts.bop_explain = this.dicts.bop_explain.map((item, index) => {
let sItem = {}
sItem.label = item.dictName
sItem.value = item.dictValue
return sItem
})
this.dicts.address_source = this.dicts.address_source.map((item, index) => {
let sItem = {}
sItem.label = item.dictName
sItem.value = item.dictValue
return sItem
})
this.dicts.msg_source = this.dicts.msg_source.map((item, index) => {
let sItem = {}
sItem.label = item.dictName
sItem.value = item.dictValue
return sItem
})












this.dicts.visualize_grade = this.dicts.visualize_grade.filter((item, index) => {
return item.dictName === 'G3' || item.dictName === 'G4'
}).map((item, index) => {
let sItem = {}
sItem.label = item.dictName
sItem.value = item.dictValue
return sItem
})
this._getInfo()
})
}
},
created () {
this._getDicts()
},
components: {
XHeader,
Tab,
TabItem,
Group,
Cell,
XInput,
Uploader,
Actionsheet,
Datetime,
XDialog
}
}



